import React from 'react'
import css from './CreateCitizenButton.module.scss'
import classnames from 'classnames'
import { toggleModal } from 'store/actions'
import FormTemplate from 'components/templates/FormTemplate/FormTemplate'
import Button from 'components/atoms/Button/Button'
import { ButtonHeights, ButtonPalettes } from 'utils/const'
import { useDispatch } from 'react-redux'

const CreateCitizenButton = ({className}) => {
  const dispatch = useDispatch()

  const namePrefix = `create-citizen`

  const formDescription = {
    lastName: {
      element: 'input',
      label: 'Прізвище',
      name: `${namePrefix}-lastName`,
      placeholder: '',
      validation: {
        required: true,
      }
    },
    firstName: {
      element: 'input',
      label: 'Ім’я',
      name: `${namePrefix}-firstName`,
      placeholder: '',
      validation: {
        required: true,
      }
    },
    middleName: {
      element: 'input',
      label: 'По батькові',
      name: `${namePrefix}-middleName`,
      placeholder: '',
      validation: {
        required: true,
      }
    },
    phone: {
      element: 'input',
      label: 'Телефоны',
      name: `${namePrefix}-phone`,
      placeholder: '',
      validation: {
        required: true,
      }
    },
    type: {
      element: 'select',
      label: 'Вулиця',
      name: `${namePrefix}-street`,
      placeholder: '',
      options: [],
      validation: {
        required: true,
      }
    },
  }

  const formDescriptionAux = {
    building: {
      element: 'select',
      label: 'Будинок',
      name: `${namePrefix}-building`,
      placeholder: '',
      options: [],
      validation: {
        required: true,
      }
    },
    entrance: {
      element: 'input',
      label: 'Підїзд',
      name: `${namePrefix}-entrance`,
      placeholder: '',
      validation: {
        required: true,
      }
    },
    apartment: {
      element: 'input',
      label: 'Квартира',
      name: `${namePrefix}-appartment`,
      placeholder: '',
      validation: {
        required: true,
      }
    },
  }

  const handleSubmitForm = data => {
    console.log(data)
  }

  const handleClickButton = () => {
    dispatch(toggleModal((
      <FormTemplate
        className={css.form}
        classNameAux={css.blockAux}
        heading={ `Додати мешканця` }
        list={Object.values(formDescription)}
        listAux={Object.values(formDescriptionAux)}
        submitFormHandler={handleSubmitForm}
      />
    )))
  }

  return (
    <Button
      className={classnames(css.button, className)}
      onClick={handleClickButton}
      palette={ButtonPalettes.TEXT}
      height={ButtonHeights.CONTENT}
    >
      + Додати мешканця
    </Button>
  )
}

export default CreateCitizenButton
